/* оформление содержимого шаблона, которое находится под
header */
.block {
  padding: 40px 0;
  position: relative; }

.block_mini {
  padding: 40px 0;
  position: relative; }

.block_map {
  padding: 40px 0 0 0;
  position: relative; }

.block_top {
  padding-top: 40px;
  position: relative; }

.block_grey {
  background-color: #f2f2f2; }

h2.block_title {
  /*  // font-size: $size; */
  font-size: 1.5625rem;
  font-family: "RobotoSlabBold", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  margin: 0 auto 20px auto;
  text-align: center; }

h2.block_title:before {
  top: 110%;
  left: 50%;
  margin-left: -50px;
  width: 100px;
  content: '';
  position: absolute;
  height: 6px;
  background-color: #f1c40f; }

h2.block_title:after {
  top: 115%;
  left: 50%;
  margin-left: -100px;
  width: 200px;
  content: '';
