  height: 2px;
  background-color: #f1c40f; }

.block_descrip {
  /*  // font-size: $size; */
  font-size: 1rem;
  text-align: center;
  margin: 0 10% 3.3% 10%; }

.creat_block {
  padding: 0;
  margin: 0; }

.creat_block_items {
  width: 49%;
  margin-right: 2%;
  float: left; }

.creat_block_items:last-child {
  margin-right: 0; }

.creat-block-img {
  padding: 0;
  margin: 0; }

.creat-block-img img {
  width: 100%;
  height: auto; }

.creat-block-title {
  font-size: 1.2em;
  text-transform: uppercase;
  margin-bottom: 35px; }

.creat-block-title span {
  color: #f1c40f; }

.creat-block-text {
  margin-bottom: 20px;
  font-size: .9em; }

/* оформление блока наши предложения  */
.our_pred_block {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap; }

.our_pred_block_items {
  width: 30%;
  margin-right: 3.3%;
  margin-bottom: 3.3%; }

.our_pred_block_items:nth-child(3n+3) {
